.App {
  height: 100vh;
}

.App-input {
  background-color: purple;
  bottom: 0px;
  display: flex;
  flex-direction: row;
  height: 30px;
  position: absolute;
  width: 100vw;
}

.App-input-field {
  flex-grow: 1;
}

.App-input-send {
  background-color: orange;
  width: 10%;
}

.App-messages {
  height: calc(100vh-30px);
}

.App-modal {
  align-items: center;
  position: fixed;
  background-color: #00000099;
  display: flex;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.App-modal-box {
  background-color: white;
  display: flex;
  width: 400px;
}

.App-modal-input {
  flex-grow: 1;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
